
import { MultiLanguage } from './language'
import { imageChange } from '@app/Infra/Util'
import EmailField from '@app/Components/Fields/Email'
import PasswordField from '@app/Components/Fields/Password'
import { buildTable2TD, BaseLoader } from '@app/Components/Images/BaseLoader'
import Popup from '@app/Components/Modals/Popup'

export default class {

    static elements() {
        return {
            formEmail: 'formEmail',
            emailID: 'emailID',
            passwordID: 'passwordID',
            passwordConfID: 'passwordConfID',
            btnNext: 'btnNext',
            btnPrev: 'btnPrev',
        };
    }

    static getHtml() {
        var elements = this.elements();
        var registerMsg = MultiLanguage(3);
        var nextMsg = MultiLanguage(16);
        var stepMsg = MultiLanguage(17);
        var prevMsg = MultiLanguage(22);
        var placeholderEmail = MultiLanguage(4);
        var placeholderEmail_label = MultiLanguage(23);
        var placeholderPass =  MultiLanguage(7)
        var placeholderPass_Title =  MultiLanguage(8)
        var label_password = MultiLanguage(7)
        var label_passwordConf = MultiLanguage(24)

        return `<div style="width:296px;" class="form-row-group-dark">
                    <div style='min-height:355px'>                        
                        <table><tr><td><h3><b>${registerMsg}</b></h3></td><td width='20px'></td><td><h4 style='color:#009E96'>${stepMsg} 2/4</h4></td></tr></table>
                        <br>
                        <div class="form-row-group" align="left" id='registerIdForm'>
                            <span style='padding-left:20px'><b>${placeholderEmail_label}</b></span>
                            ${EmailField.getHtml(elements.emailID, placeholderEmail)}
                            <br>
                            <span style='padding-left:20px'><b>${label_password}</b></span>
                            ${PasswordField.getHtml(elements.passwordID, placeholderPass, placeholderPass_Title)}
                            <span style='padding-left:20px'><b>${label_passwordConf}</b></span>
                            ${PasswordField.getHtml(elements.passwordConfID, placeholderPass, placeholderPass_Title)}
                            <br>
                        </div>
                        <br>
                    </div>
                    <div>
                        <table width='100%'>
                            <tr>
                                <td><a id="${elements.btnPrev}" class="button circle block green">${prevMsg}</a></td>
                                <td width='20px'></td>
                                <td><a id="${elements.btnNext}" class="button circle block green">
                                        ${buildTable2TD(nextMsg, BaseLoader(), elements.btnNext)}
                                    </a>
                                </td>
                            </tr>
                        </table>
                    </div>
                    <br>
                    <br>
                </div>${Popup.getHtml()}`;
    }

    static validate(_params) {
        var elements = this.elements();
        var valPass = PasswordField.validate(elements.passwordID, _params, MultiLanguage(5), MultiLanguage(9));
        var valConfPass = PasswordField.validate(elements.passwordConfID, _params, MultiLanguage(5), MultiLanguage(9));
        var valEmail = EmailField.validate(elements.emailID, _params, MultiLanguage(5), MultiLanguage(6));

        if (valPass) {
            valConfPass = $('#' + elements.passwordID).val() == $('#' + elements.passwordConfID).val()
            if (!valConfPass)
                imageChange('#fail' +  elements.passwordConfID, 'lock_err.png')
        }

        if (!(valEmail && valPass && valConfPass))
            return false;
        return true;
    }
}
